  /*
    <div class="docs-md">
        <h1 id="upgrading-from-hifi" class="docs-md__h1">Upgrading from Hifi</h1>
      <p>If you're coming from <a href="http://github.com/nypublicradio/ember-hifi"  class="docs-md__a">ember-hifi</a>, welcome!</p>
  
        <h3 id="why-upgrade-" class="docs-md__h3"><a href="#why-upgrade-" class="heading-anchor">Why upgrade?</a></h3>
      
          <ol class="docs-list-decimal"><li>Handy template helpers make things ridiculously simpler!</li>
  <li>Stereo uses <a href="http://ember-concurrency.com"  class="docs-md__a">ember-concurrency</a> tasks instead of the home rolled mixture of rat-nested promises I devised back in 2015 when that sort of thing was in vogue. If you're using <code>ember-concurrency</code> tasks in your app you can change your <code>play</code> and <code>load</code> calls to use the task versions, at <code>this.stereo.playTask</code> and <code>this.stereo.loadTask</code></li>
  <li>Improved tooling to help deal with autoplay problems. An <code>audio-blocked</code> event was added, as well as an <code>autoplay-allowed</code> helper and a <code>sound-is-blocked</code> helper to help deal with autoplay issues. (<a href="https://github.com/nypublicradio/ember-hifi/issues/44"  class="docs-md__a">fixes hifi issue #44</a>)</li>
  <li>Better defaults. Volume is now by default at 100, aka system volume. You can set <code>initialVolume: 50</code> in the environment configuration if you want to keep it at its former level.</li>
  <li>Better docs, better tests, better future! (<a href="https://github.com/nypublicradio/ember-hifi/issues/25"  class="docs-md__a">fixes hifi issue #25</a>)</li>
  </ol>
        
        <h3 id="upgrade-your-existing-app" class="docs-md__h3"><a href="#upgrade-your-existing-app" class="heading-anchor">Upgrade your existing app</a></h3>
      
          <ol class="docs-list-decimal"><li>Find anything that says "hifi", "ember-hifi", or "emberHifi" and rename it "stereo", "ember-stereo", or "emberStereo", respectively. The hifi service is probably the one you'll have the most of, and maybe your environment config.</li>
  <li>Any event handlers that were expecting <code>(sound)</code> should now have a new signature and need to be changed to <code>({sound})</code>
  e.g.</li>
  </ol>
        <pre class="docs-md__code"><code class="language-js"><span class="hljs-comment">//BEFORE</span>
  <span class="hljs-variable language_">this</span>.<span class="hljs-property">hifi</span>.<span class="hljs-title function_">on</span>(<span class="hljs-string">&#x27;event-name&#x27;</span>, <span class="hljs-function">(<span class="hljs-params">sound</span>) =&gt;</span> {
    <span class="hljs-comment">/* handler *\/</span>
  });
  
  <span class="hljs-comment">//AFTER</span>
  <span class="hljs-variable language_">this</span>.<span class="hljs-property">stereo</span>.<span class="hljs-title function_">on</span>(<span class="hljs-string">&#x27;event-name&#x27;</span>, <span class="hljs-function">(<span class="hljs-params">{ sound }</span>) =&gt;</span> {
    <span class="hljs-comment">/* handler *\/</span>
  });
  </code></pre>
  
          <ol class="docs-list-decimal"><li>Whatever tricks you had implement to do in order to catch that uncatchable 'All Promises Failed' error in <code>hifi</code> you can remove them. <code>play</code> and <code>load</code> requests will now fail sanely. And if you don't want them to throw errors at all, instead returning the error as part of the response, you can pass <code>silenceErrors: true</code> as an option. (fixes hifi issue <a href="https://github.com/nypublicradio/ember-hifi/issues/86"  class="docs-md__a">#86</a> and <a href="https://github.com/nypublicradio/ember-hifi/issues/16"  class="docs-md__a">#16</a>)</li>
  <li><code>this.stereo.connections</code> has been renamed to <code>this.stereo.connectionNames</code>. <code>this.stereo.connections</code> now returns the actual connection objects.</li>
  </ol>
        </div>
  */
