  /*
    <div class="docs-md">
        <h1 id="what-s-in-the-box" class="docs-md__h1">What's in the box</h1>
      <p>A high level overview of what's in this addon</p>
  
        <h3 id="service" class="docs-md__h3"><a href="#service" class="heading-anchor">Service</a></h3>
      <p>The <code>stereo</code> service manages loading and playing sounds, making sure that only one sound plays at at time, setting volume, and providing system-level events your application can tie into.</p>
  
        <h3 id="template-action-helpers" class="docs-md__h3"><a href="#template-action-helpers" class="heading-anchor">Template Action Helpers</a></h3>
      <p>These are the helpers that start with a verb, and you'll use them to perform an action on a sound/url.</p>
  <p><a href="/docs/api/helpers/load-sound"  class="docs-md__a"><code>load-sound</code></a>, <a href="/docs/api/helpers/play-sound"  class="docs-md__a"><code>play-sound</code></a>, <a href="/docs/api/helpers/toggle-play-sound"  class="docs-md__a"><code>toggle-play-sound</code></a>, <a href="/docs/api/helpers/seek-sound"  class="docs-md__a"><code>seek-sound</code></a>, <a href="/docs/api/helpers/rewind-sound"  class="docs-md__a"><code>rewind-sound</code></a>, <a href="/docs/api/helpers/fastforward-sound"  class="docs-md__a"><code>fastforward-sound</code></a>, and <a href="/docs/api/helpers/stop-sound"  class="docs-md__a"><code>stop-sound</code></a> are all basically used like this:</p>
  
  <p><Docs::PlayButtonNoStatus @identifier="/sounds/tic-tac-toe.mp3" /></p>
  
        <h3 id="template-helpers" class="docs-md__h3"><a href="#template-helpers" class="heading-anchor">Template Helpers</a></h3>
      <p>These are the helpers that generally start with <code>sound</code>, and you'll use them to get information about a sound/url.</p>
  <p><a href="/docs/api/helpers/sound-is-playing"  class="docs-md__a"><code>sound-is-playing</code></a>, <a href="/docs/api/helpers/sound-is-loading"  class="docs-md__a"><code>sound-is-loading</code></a>, <a href="/docs/api/helpers/sound-is-loaded"  class="docs-md__a"><code>sound-is-loaded</code></a>, <a href="/docs/api/helpers/sound-is-errored"  class="docs-md__a"><code>sound-is-errored</code></a>, <a href="/docs/api/helpers/sound-is-seekable"  class="docs-md__a"><code>sound-is-seekable</code></a>, <a href="/docs/api/helpers/sound-is-rewindable"  class="docs-md__a"><code>sound-is-rewindable</code></a>, <a href="/docs/api/helpers/sound-is-fastforwardable"  class="docs-md__a"><code>sound-is-fastforwardable</code></a>, <a href="/docs/api/helpers/sound-is-blocked"  class="docs-md__a"><code>sound-is-blocked</code></a>, <a href="/docs/api/helpers/sound-position"  class="docs-md__a"><code>sound-position</code></a>, <a href="/docs/api/helpers/sound-duration"  class="docs-md__a"><code>sound-duration</code></a>, <a href="/docs/api/helpers/sound-metadata"  class="docs-md__a"><code>sound-metadata</code></a>, <a href="/docs/api/helpers/sound-error-details"  class="docs-md__a"><code>sound-error-details</code></a>, and <a href="/docs/api/helpers/find-sound"  class="docs-md__a"><code>find-sound</code></a> can be quickly composed control audio playback right in the template.</p>
  <p><a href="/docs/api/helpers/find-sound"  class="docs-md__a"><code>find-sound</code></a> returns a loaded sound object given an identifier, and <a href="/docs/api/helpers/current-sound"  class="docs-md__a"><code>current-sound</code></a> returns the currently playing/paused sound. <a href="/docs/api/helpers/stereo-volume"  class="docs-md__a"><code>stereo-volume</code></a> returns the current system audio level.</p>
  <p>All these helpers take an identifier as an argument which can be a <code>url</code> string, an object with a <code>url</code> property, a loaded sound object, or even a promise that resolves to one of the previous items.</p>
  
  <p><Docs::PlayButtonWithStatus @identifier="/sounds/tic-tac-toe.mp3" /></p>
  
        <h3 id="modifiers" class="docs-md__h3"><a href="#modifiers" class="heading-anchor">Modifiers</a></h3>
      <p>There are a couple of modifiers to help in creating a volume and a position control. <a href="/docs/api/helpers/stereo-volume"  class="docs-md__a"><code>&#123;&#123;stereo-volume&#125;&#125;</code></a> can transform a range control into a volume control, and <a href="/docs/api/helpers/sound-position-slider"  class="docs-md__a"><code>&#123;&#123;sound-position-slider&#125;&#125;</code></a> can turn an element into a position control.</p>
  
        <h2 id="stereo-connections" class="docs-md__h2"><a href="#stereo-connections" class="heading-anchor">Stereo Connections</a></h2>
      <p>Stereo <code>connections</code> are plugins for <code>ember-stereo</code> that know how to load particular sound formats. Different devices have different audio capabilities, so given a url the <code>stereo</code> service will try each loaded connection until one succeeds. Generally, stereo always tries playing the sound using a native audio element before trying anything else.</p>
  
          <ul class="docs-list-disc"><li><code>NativeAudio</code>: uses the native <code>&lt;audio&gt;</code> element to play sounds. This is prioritized automatically on mobile devices and can play any format the user's browser supports.</li>
  <li><code>Howler</code>: uses <a href="https://github.com/goldfire/howler.js"  class="docs-md__a">howler.js</a> to play sounds.</li>
  <li><code>HLS</code> uses <a href="https://github.com/video-dev/hls.js/"  class="docs-md__a">HLS.js</a> to play <a href="https://caniuse.com/http-live-streaming"  class="docs-md__a">.hls streams</a>.</li>
  </ul>
        <p>Stereo offers three connection options, but only the native audio connection is included by default (to keep bundle size small). To include other audio connections, add them to your environment.js in the order you want them tried.</p>
  <pre class="docs-md__code"><code class="language-js">  <span class="hljs-attr">emberStereo</span>: {
      <span class="hljs-attr">initialVolume</span>: <span class="hljs-number">100</span>, <span class="hljs-comment">// default = 100</span>
      <span class="hljs-attr">silenceErrors</span>: <span class="hljs-literal">false</span> <span class="hljs-comment">// true = silence thrown audio errors app wide, and handle them inline</span>
      <span class="hljs-attr">connections</span>: [ <span class="hljs-comment">// default = &#x27;NativeAudio&#x27; only</span>
        {<span class="hljs-attr">name</span>: <span class="hljs-string">&#x27;NativeAudio&#x27;</span>},
        {<span class="hljs-attr">name</span>: <span class="hljs-string">&#x27;Howler&#x27;</span>},
        {<span class="hljs-attr">name</span>: <span class="hljs-string">&#x27;HLS&#x27;</span>}
      ]
    }
  </code></pre>
  </div>
  */
